











































import Vue from 'vue'
import { mapActions, mapGetters } from 'vuex'
import { findIndex, isMatch } from 'lodash'

export default Vue.extend({
  name: 'PageHome',
  computed: {
    ...mapGetters(['sultans'])
  },
  methods: {
    ...mapActions(['AddSultan', 'ToggleSultan'])
  },
  created () {
    this.$store.dispatch('GetSultans')
  }
})
