


























































import Vue from 'vue'
import { filter, get, map } from 'lodash'

export default Vue.extend({
  name: 'app-drawer',
  props: {
    drawer: { required: true },
    isAdmin: { required: true, type: Boolean }
  },
  computed: {
    activeSultanNames () {
      return map(
        filter(get(this, 'sultans'), sultan => Number(sultan.active)),
        'name'
      )
    },

    sultans () {
      return get(this.$store, 'getters.sultans')
    }
  },
  created () {
    const actions: any = get(this, '$store.dispatch')
    actions('GetSultans')
  }
})
